<template>
  <div class="article">
    <!-- 如果正在加载，则显示此此div -->
      <div class="loading" v-if='isLoading'>
        <img src="" alt="">
      </div>
      <div v-else>
        <div class="topic_header">
          <div class="topic_title">{{post.title}}</div>
          <ul>
            <li>•发布于：{{post.create_at | formatDate}}</li>
            <li>•作者{{post.author.loginname}}</li>
            <li>•{{post.visit_count}}次浏览</li>
            <li>•{{post |tabFormat}}</li>
          </ul>
          <div v-html="post.content" class="topic_content"></div>
        </div>
        <div id="reply">
          <div class="topbar">回复</div>
          <div v-for="(reply,index) in post.replies" class="replySec">
            <div class="replyUp">
              <router-link :to='{
                name:"user_info",    //要去到的路由
                params:{
                  name:reply.author.loginname
                }
              }' >
                <img :src="reply.author.avatar_url" alt="">
              </router-link>
              <router-link :to="{
                name:'user_info',
                params:{
                  name:reply.author.loginname
                }
              }">
                <span>{{reply.author.loginname}}</span>
              </router-link>
              <span>{{index+1}}楼</span>
              <span v-if="reply.ups.length>0">☝{{reply.ups.length}}</span>
              <span v-else></span>
            </div>
            <p v-html='reply.content'></p>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  name:'Article',
  data(){
    return{
      isLoading: false,
      post:{}    //代表当前页的所有内容
    }
  },
  methods:{
    getArticleData(){
      this.$http.get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
        .then((result)=>{
          if(result.data.success === true){
            this.isLoading = false
            this.post = result.data.data
          }
        })
        .catch(function(err){
          console.log(err)
        })
    }
  },
  beforeMount(){
    this.isLoading = true
    this.getArticleData()
  },
  //最近回复和主题点击不能跳转的解决办法
  watch:{
    '$route'(to,from){
      this.getArticleData()
    }
  }
}
</script>


<style >
  @import url('./../assets/markdown-github.css');
  .topbar {
    padding: 10px;
    background-color: #f6f6f6;
    height: 16px;
    font-size: 12px;
    margin-top: 10px;
  }

  .article:not(:first-child) {
    margin-right: 340px;
    margin-top: 15px;
  }

  #reply, .topic_header {
    background-color: #fff;
  }

  #reply {
    margin-top: 15px;
  }

  #reply img {
    width: 30px;
    height: 30px;
    position: relative;
    bottom: -9px;
  }

  #reply a, #reply span {
    font-size: 13px;
    color: #666;
    text-decoration: none;
  }
  .replySec{
    border-bottom:1px solid #e5e5e5;
    padding:0 10px;
  }

  /* .loading {
    text-align: center;
    padding-top: 300px;
  } */

  .replyUp a:nth-of-type(2) {
    margin-left: 0px;
    display: inline-block;
  }

  .topic_header {
    padding: 10px;
  }

  .topic_title {
    font-size: 20px;
    font-weight: bold;
    padding-top: 8px;
  }

  .topic_header ul {
    list-style: none;
    padding: 0px 0px;
    margin: 6px 0px;
  }

  .topic_header li {
    display: inline-block;
    font-size: 12px;
    color: #838383;
  }

  .topic_content {
    border-top: 1px solid #e5e5e5;
    padding: 0 10px;
  }

  .markdown-text img {
    width: 92% !important;
  }
</style>


